<script>
import { GET_USER, GET_CLIENT } from '@src/apollo/queries' //, GET_ACCOUNTING_FIRMS
import GxLoader from '@components/_Core/Utils/GxLoader'
import Layout from '@layouts/LayoutWebAccounting'
import GxCardModal from '@src/components/_Core/Cards/GxCardModal'
import { getters, mutations } from '@src/store'
export default {
  name: 'masterAccounting',
  components: {
    Layout,
    // MenuBottom,
    GxLoader,
    GxCardModal,
  },
  data() {
    return {
      myAccount: null,
      myAccountingFirms: null,
      todos: null,
      error: false,
      errorImg: require('@assets/images/Dashboard/De_errorplataforma.svg'),
      isCollapsed: false,
      typeAside: 2,
    }
  },
  computed: {
    ...getters,
    typeAsideMain() {
      return this.typeAside
    },
  },
  apollo: {
    myAccount: GET_USER,
    myAccountingFirms: {
      query: GET_CLIENT,
      variables() {
        return {
          clientNumber: localStorage.getItem('clientNumber'),
        }
      },
      fetchPolicy: 'no-cache',
    },
  },
  created() {
    this.$root.$on('changeAsideMenu', this.changeAsideMenu)
    console.log('Init subs web.vue created')
    //restartWebsockets(this.$apollo.provider.clients.defaultClient.wsClient)
    this.startSubs = true
    /* eslint-enable */
  },
  methods: {
    ...mutations,
    changeAsideMenu(_paramTypeAside) {
      this.typeAside = _paramTypeAside
    },
  },
}
</script>
<template>
  <!-- v-if="
      company && company.branchNumber && company.number && myAccount && todos
    " -->
  <div v-if="myAccount && myAccountingFirms" id="web-layout">
    <GxCardModal
      v-if="headerError"
      :headline="headerError"
      :card-img="imageError"
      :text-body="bodyError"
      :alt="headerError"
      @click="
        modalButtons
          ? cleanErrorState($router, 'logout')
          : cleanErrorState($router, $route.name)
      "
      @close="
        modalButtons
          ? cleanErrorState($router, 'logout')
          : cleanErrorState($router, $route.name)
      "
    >
      <template v-if="modalButtons" v-slot:actions>
        <div class="flex justify-end mt-4">
          <GxButton plain @click.stop="cleanErrorState($router, 'logout')"
            >ENTENDIDO</GxButton
          >
        </div>
      </template>
    </GxCardModal>
    <!-- Desktop -->
    <!-- :my-account="myAccount"
      :todos="todos.list" -->
    <Layout
      v-if="$mq === 'de'"
      :myAccount="myAccount"
      :myAccountingFirms="myAccountingFirms"
      no-horizontal-padding
      has-drawer
      :typeAside="typeAsideMain"
    >
      <RouterView v-if="this.myAccount" :typeAside="typeAside" />
    </Layout>
    <!-- Mobile -->
    <RouterView v-else-if="this.myAccount" :typeAside="typeAside" />
    <!-- :todos="todos.list" -->
    <!-- <MenuBottom v-if="showBottomMenu && $mq === 'mo'" class="z-0" /> -->
  </div>
  <div v-else id="web-layout" class="flex items-center justify-center">
    <GxCardModal
      v-if="headerError"
      :headline="headerError"
      :card-img="imageError"
      :text-body="bodyError"
      alt="Error"
      @click="
        modalButtons
          ? cleanErrorState($router, 'logout')
          : cleanErrorState($router, $route.name)
      "
      @close="
        modalButtons
          ? cleanErrorState($router, 'logout')
          : cleanErrorState($router, $route.name)
      "
    >
      <template v-if="modalButtons" v-slot:actions>
        <div class="flex justify-end mt-4">
          <GxButton plain @click.stop="cleanErrorState($router, 'logout')"
            >ENTENDIDO</GxButton
          >
        </div>
      </template>
    </GxCardModal>
    <GxLoader />
  </div>
</template>
