<script>
//import NotificationIcon from '@components/Desktop/NotificationIcon'
import GxCalendar from '@components/_CoreTaxes/Calendar/GxCalendar'
import ProfileIcon from '@components/Desktop/ProfileIcon'
import AsideMain from '@src/router/layouts/AsideMainAccounting.vue'
import { GET_RANGE_OF_PERIODS } from '@src/apollo/queries'
// import GxIcon from '@components/_Core/Icons/GxIcon'
export default {
  name: 'LayoutWebAccounting',
  components: {
    //NotificationIcon,
    ProfileIcon,
    AsideMain,
    GxCalendar,
  },
  data() {
    return {
      year: null,
      month: null,
      daysActions: null,
      beginningPeriod: null,
      endingPeriod: null,
      hasScrolled: false,
      activeDeskTopBarMenu: null,
      drawerLeft: false,
      mobileSearch: false,
      drawerRight: false,
      customTopBar: false,
      drawerOPen: false,
      search: '',
      myjournalEntries: 'myjournalEntries',
    }
  },
  props: {
    dark: {
      type: Boolean,
      default: false,
    },
    breadcrumb: {
      type: Boolean,
      default: false,
    },
    hasSearch: {
      type: Boolean,
      default: false,
    },
    onSearchClasses: {
      type: String,
      default: 'text-black',
    },
    placeholder: {
      type: String,
      default: null,
    },
    hasDrawer: {
      type: Boolean,
      default: false,
    },
    hasDrawerRight: {
      type: Boolean,
      default: false,
    },
    hasStickyElement: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: null,
    },
    icon: {
      type: String,
      default: '',
    },
    iconClassWithoutScroll: {
      type: String,
      default: 'text-black',
    },
    iconClassWithScroll: {
      type: String,
      default: 'text-primary',
    },
    topBarClassWithScroll: {
      type: String,
      default: '',
    },
    topBarClassWithoutScroll: {
      type: String,
      default: 'text-black bg-white',
    },
    iconClickHandler: {
      type: Function,
      default: null,
    },
    iconRight: {
      type: String,
      default: null,
    },
    iconRightClickHandler: {
      type: Function,
      default: null,
    },
    withPaddingBottom: {
      type: Boolean,
      default: true,
    },
    noHorizontalPadding: {
      type: Boolean,
      default: false,
    },
    tabs: {
      type: Boolean,
      default: false,
    },
    breadcrumbClassesWithScroll: {
      type: Object,
      default: () => ({
        icon: 'text-gray-700',
        next: 'text-black',
        active: 'text-primary',
      }),
    },
    breadcrumbClassesWithoutScroll: {
      type: Object,
      default: () => ({
        icon: 'text-gray-700',
        next: 'text-black',
        active: 'text-primary',
      }),
    },
    myAccount: {
      type: Object,
      default: null,
    },
    myAccountingFirms: {
      type: Array,
      default: null,
    },
    todos: {
      type: Array,
      default: () => [],
    },
    typeAside: {
      type: Number,
      require: true,
    },
  },
  apollo: {
    getRangeOfPeriods: {
      query: GET_RANGE_OF_PERIODS,
      variables() {
        return {
          clientNumber: [localStorage.getItem('clientNumber')],
        }
      },
      update(data) {
        this.beginningPeriod =
          data.myAccountingFirms[0].clients[0].accounting.beginningPeriod
        this.endingPeriod =
          data.myAccountingFirms[0].clients[0].accounting.endingPeriod

        localStorage.setItem('beginningPeriod', this.beginningPeriod)
        localStorage.setItem('endingPeriod', this.endingPeriod)
      },
      fetchPolicy: 'no-cache',
    },
  },
  computed: {
    hasTopBar() {
      return this.title || this.icon || this.iconRight || this.hasDrawer
    },
    getName() {
      return this.myAccountingFirms[0].commercialName
        ? this.myAccountingFirms[0].commercialName
        : this.myAccountingFirms[0].legalName
    },
  },
  watch: {
    search(newValue) {
      if (this.$mq === 'mo') {
        this.$emit('search', newValue)
      }
    },
  },
  created() {
    this.$root.$off('setPeriod', this.setPeriod)
    this.$root.$off('setPeriodDays', this.setPeriodDays)

    this.$root.$on('setPeriod', this.setPeriod)
    this.$root.$on('setPeriodDays', this.setPeriodDays)

    if (!this.myAccountingFirms) this.goToDashboard()
    if (!this.myAccountingFirms[0].clients) this.goToDashboard()
    if (
      this.myAccountingFirms[0].clients.length === 0 ||
      this.myAccountingFirms[0].clients.length > 1
    ) {
      this.goToDashboard()
    }
    this.year = new Date().getFullYear()
    this.month = new Date().getMonth() + 1
    localStorage.removeItem('periodDefault')
  },
  mounted() {
    localStorage.removeItem('periodDefault')
    // if (this.$route.query.drawerRight) {
    //   this.$nextTick(() => {
    //     this.drawerRight = true
    //     let query = Object.assign({}, this.$route.query)
    //     delete query.drawerRight
    //     this.$router.replace({ query })
    //   })
    // }
    // let lastKnownScrollPosition = 0
    // let ticking = false
    // if (this.$mq === 'de') {
    //   if (document.getElementById('gxDeMain')) {
    //     document.getElementById('gxDeMain').addEventListener('scroll', (e) => {
    //       lastKnownScrollPosition = e.target.scrollTop
    //       if (!ticking) {
    //         window.requestAnimationFrame(() => {
    //           this.hasScrolled = lastKnownScrollPosition > 0
    //           ticking = false
    //         })
    //         ticking = true
    //       }
    //     })
    //   }
    // } else {
    //   window.addEventListener('scroll', (e) => {
    //     lastKnownScrollPosition = window.scrollY
    //     if (!ticking) {
    //       window.requestAnimationFrame(() => {
    //         this.hasScrolled = lastKnownScrollPosition > 0
    //         ticking = false
    //       })
    //       ticking = true
    //     }
    //   })
    // }
  },
  beforeDestroy() {
    this.$root.$off('setPeriod', this.setPeriod)
    this.$root.$off('setPeriodDays', this.setPeriodDays)
  },
  methods: {
    setPeriod(_year, _month) {
      this.year = _year
      this.month = _month
    },
    setPeriodDays(days) {
      this.daysActions = days
    },
    onActiveMenu(componentName) {
      this.activeDeskTopBarMenu = componentName
    },
    goToDashboard() {
      localStorage.removeItem('clientNumber')
      localStorage.removeItem('beginningPeriod')
      localStorage.removeItem('endingPeriod')
      localStorage.removeItem('periodDefault')

      this.$router.push({
        name: 'dashboard',
      })
    },
    changeYearMonth(_year, _month) {
      this.$root.$emit('changeYearMonth', Number(_year), Number(_month))
    },
  },
}
</script>

<template>
  <MqLayout
    id="layoutDesk"
    key="layoutDesk"
    mq="de"
    class="w-screen bg-white font-nunito"
  >
    <nav
      v-if="hasTopBar"
      class="sticky top-0 z-10 flex items-center w-full h-8 bg-white gxDesktopTopNav gx-h5 shadow-2"
      role="navigation"
    >
      <div class="flex items-center justify-between flex-grow pr-4">
        <div class="flex items-center self-stretch pl-4 de:gx-caption">
          <!-- @click.prevent.stop="editLedgerAccount(null)" -->
          <GxIcon
            :icon="'home'"
            class="cursor-pointer mr-2"
            :title="'Home'"
            @click="goToDashboard"
            v-bind:style="{ fontSize: '18px' }"
          />
          <p
            v-if="myAccount"
            class="pr-4 mr-4 font-bold border-r border-gray-300 border-solid"
            >{{ getName ? getName : 'Sin configurar' }}
          </p>
          <p
            v-else
            class="pr-4 mr-4 font-bold text-transparent border-r border-gray-300 border-solid"
            >placeholder</p
          >
        </div>
        <div class="flex items-center">
          <!-- <NotificationIcon
            :active-menu="activeDeskTopBarMenu"
            class="mr-2"
            @activeMenu="onActiveMenu"
          /> -->
          <ProfileIcon
            :my-account="myAccount"
            :active-menu="activeDeskTopBarMenu"
            @activeMenu="onActiveMenu"
          />
        </div>
      </div>
    </nav>
    <main
      id="gxDeMainNotStickyElement"
      class="flex flex-col w-full gxDeMain de:pt-12"
      :class="{
        'px-8': !noHorizontalPadding,
      }"
    >
      <slot />
    </main>
    <AsideMain
      v-if="this.myAccountingFirms"
      class="z-1 gxDeAside"
      :client="this.myAccountingFirms[0].clients[0]"
    >
      <!-- v-slot:[myjournalEntries] -->
      <!-- <template v-slot:default="[myjournalEntries]">
        <h1>Hola mundo</h1>
      </template> -->
      <template v-slot:myjournalEntries>
        <GxCalendar
          v-if="year && month && beginningPeriod && endingPeriod"
          :year="year"
          :month="month"
          :dateMin="beginningPeriod"
          :dateMax="endingPeriod"
          :daysActions="daysActions"
          @changeYearMonth="changeYearMonth"
        />
      </template>
    </AsideMain>
  </MqLayout>
</template>
