<script>
export default {
  name: 'GxCalendarDay',
  data() {
    return {
      html: '',
      // initDay: null,
      // lastDay: null,
    }
  },
  created() {
    // this.initDay = new Date(this.year, this.month, 1).getDay()
    // this.lastDay = new Date(this.year, this.month, 0).getDate()
    // console.log(this.month)
    // console.log(this.initDay)
    // console.log(this.lastDay)
  },
  props: {
    month: {
      type: Number,
      default: null,
    },
    year: {
      type: Number,
      default: null,
    },
    daysActions: {
      type: Array,
      default: null,
    },
  },
  watch: {
    daysActions: {
      handler() {
        this.getTable()
        //console.log(newVal)
        //console.log('entro 2')
      },
      immediate: true,
    },
  },
  computed: {
    initDay() {
      return new Date(this.year, this.month - 1, 1).getDay()
    },
    lastDay() {
      //console.log(new Date(this.year, this.month, 0))
      return new Date(this.year, this.month, 0).getDate()
    },
    isYearAndMonthCorrency() {
      return (
        this.year === new Date().getFullYear() &&
        this.month === new Date().getMonth() + 1
      )
    },
  },
  methods: {
    searchDay(_day) {
      if (this.daysActions)
        if (this.daysActions.findIndex((d) => d.dayOfTheMonth === _day) > -1) {
          return this.daysActions[
            this.daysActions.findIndex((d) => d.dayOfTheMonth === _day)
          ]
        }
      return null
    },
    getTable() {
      //console.log(this.isYearAndMonthCorrency)
      //console.log(this.daysActions)
      let dayCurrency = null
      if (this.isYearAndMonthCorrency) {
        dayCurrency = new Date().getDate()
      }
      let indexColumn = 0
      let day = 1 - this.initDay
      this.html = ''
      let searchActions = null
      while (day <= this.lastDay) {
        //dayWithMovements
        this.html += '<tr>'
        for (indexColumn = 0; indexColumn < 7; indexColumn++) {
          if (!(day <= 0 || day > this.lastDay))
            searchActions = this.searchDay(day)
          //console.log(searchActions)
          this.html +=
            '<td class="h-10">' +
            '<div class="flex flex-col pt-1 pb-px h-8 rounded-lg ' +
            (dayCurrency == day ? 'bg-gray-300' : '') +
            (day <= 0 || day > this.lastDay
              ? ''
              : searchActions === null
              ? ''
              : searchActions.dayWithMovements
              ? 'hover:bg-gray-300 cursor-pointer font-black'
              : '') +
            '">' +
            '<div class="text-base ' +
            (searchActions === null
              ? ''
              : searchActions.dayWithMovements
              ? 'font-bold'
              : 'font-bold text-gray-500') +
            '">' +
            (day <= 0 || day > this.lastDay ? '' : day) +
            '</div>' +
            (day <= 0 || day > this.lastDay
              ? ''
              : '<div class="-mt-3">' +
                (searchActions === null
                  ? ''
                  : (!searchActions.alert
                      ? ''
                      : '<span class="text-sm text-alert h-1">•</span>') +
                    (!searchActions.warning
                      ? ''
                      : '<span class="text-sm text-pending h-1">•</span>')) +
                '</div>') +
            '</div>' +
            '</td>'
          day++
        }
        this.html += '</tr>'
      }
      //console.log(this.html)
      //console.log('entro 1')
      // return html
    },
  },
}
</script>

<template>
  <div class="flex flex-col">
    <table class="text-center">
      <thead class="text-gray-700">
        <tr
          ><th class="w-10 font-bold">Dom</th>
          <th class="w-10 font-bold">Lun</th>
          <th class="w-10 font-bold">Mar</th>
          <th class="w-10 font-bold">Mie</th>
          <th class="w-10 font-bold">Jue</th>
          <th class="w-10 font-bold">Vie</th>
          <th class="w-10 font-bold">Sab</th>
        </tr>
      </thead>
      <tbody ref="tbody" v-html="html">
        <!-- <slot>
          {{ getTable }}
        </slot> -->
      </tbody>
    </table>
  </div>
</template>
