<script>
// import { GET_PERIODS } from '@src/apollo/queries'
// import { mount } from '@vue/test-utils'
// import GxCardModal from '@src/components/_Core/Cards/GxCardModal'
// import { CONFIG_SET_BEGINNING_PERIOD } from '@src/apollo/mutations'
import GxCalendarDay from '@components/_CoreTaxes/Calendar/GxCalendarDay.vue'

export default {
  name: 'HomeCalendar',
  data() {
    return {
      yearCurrency: new Date().getFullYear(),
      monthCurrency: new Date().getMonth(),
      // dateMin: null, //FORMAT YYYYMM
      // dateMax: null, //FORMAT YYYYMM
    }
  },
  props: {
    year: {
      //YYYY
      type: Number,
      default: null,
    },
    month: {
      //MM
      type: Number,
      default: null,
    },
    dateMin: {
      //YYYYMM
      type: Number,
      default: null,
    },
    dateMax: {
      //YYYYMM
      type: Number,
      default: null,
    },
    daysActions: {
      type: Array,
      default: null,
    },
  },
  created() {
    if (this.year) this.yearCurrency = this.year
    if (this.month) this.monthCurrency = this.month

    if (this.dateMin && this.dateMax)
      if (
        new Date(
          new Date().getFullYear(),
          new Date().getMonth() + 1,
          new Date().getDate()
        ) >
          new Date(
            Number(this.dateMax.toString().substring(0, 4)),
            Number(this.dateMax.toString().substring(4)),
            0
          ) ||
        new Date(
          new Date().getFullYear(),
          new Date().getMonth() + 1,
          new Date().getDate()
        ) <
          new Date(
            Number(this.dateMin.toString().substring(0, 4)),
            Number(this.dateMin.toString().substring(4)),
            0
          )
      ) {
        this.yearCurrency = Number(this.dateMax.toString().substring(0, 4))
        this.monthCurrency = Number(this.dateMax.toString().substring(4))
      }
  },
  components: {
    GxCalendarDay,
  },
  computed: {
    enableNext() {
      return !(
        Number(
          this.yearCurrency +
            (this.monthCurrency < 10
              ? '0' + this.monthCurrency
              : this.monthCurrency
            ).toString()
        ) >= this.dateMax
      )
    },
    enableBack() {
      return !(
        Number(
          this.yearCurrency +
            (this.monthCurrency < 10
              ? '0' + this.monthCurrency
              : this.monthCurrency
            ).toString()
        ) <= this.dateMin
      )
    },
    getMonthName() {
      let nameMonths = [
        'Enero',
        'Febrero',
        'Marzo',
        'Abril',
        'Mayo',
        'Junio',
        'Julio',
        'Agosto',
        'Septiembre',
        'Octubre',
        'Noviembre',
        'Diciembre',
      ]
      return nameMonths[this.monthCurrency - 1]
    },
  },
  watch: {
    year(newVal) {
      this.yearCurrency = newVal
    },
    month(newVal) {
      this.monthCurrency = newVal
    },
    immediate: true,
  },
  methods: {
    nextMonth() {
      if (this.enableNext) {
        if (this.monthCurrency === 12) {
          this.monthCurrency = 1
          this.yearCurrency++
        } else this.monthCurrency++
        this.changeYearMonth()
      }
    },
    backMonth() {
      if (this.enableBack) {
        if (this.monthCurrency === 1) {
          this.monthCurrency = 12
          this.yearCurrency--
        } else this.monthCurrency--
        this.changeYearMonth()
      }
    },
    changeYearMonth() {
      this.$emit('changeYearMonth', this.yearCurrency, this.monthCurrency)
    },
  },
}
</script>

<template>
  <div
    class="flex flex-col min-h-0 w-70 rounded-lg shadow-2 pl-1 border bg-white"
  >
    <div
      class="flex flex-row items-stretch text-black content-center max-w-2xl text-base mt-5"
    >
      <div class="flex-1 text-right mt-px"
        ><GxIcon
          :icon="'keyboard_arrow_left'"
          class="cursor-pointer mr-2"
          :class="{
            'text-gray-300': !enableBack,
            'text-gray-black': enableBack,
          }"
          :title="'Settings'"
          v-bind:style="{ fontSize: '22px' }"
          @click="backMonth"
      /></div>
      <div class="text-center font-bold w-36">
        {{ getMonthName }} {{ yearCurrency }}
      </div>
      <div class="flex-1 text-left mt-px"
        ><GxIcon
          :icon="'keyboard_arrow_right'"
          class="cursor-pointer ml-2"
          :class="{
            'text-gray-300': !enableNext,
            'text-gray-black': enableNext,
          }"
          :title="'Settings'"
          v-bind:style="{ fontSize: '22px' }"
          @click="nextMonth"
      /></div>
    </div>
    <div class="flex content-start flex-wrap w-69 mt-3 pb-1">
      <GxCalendarDay
        :year="this.yearCurrency"
        :month="this.monthCurrency"
        :daysActions="daysActions"
      />
    </div>
  </div>
</template>
