<script>
// import GxCalendar from '@components/_CoreTaxes/Calendar/GxCalendar'
export default {
  name: 'AsideMainAccounting',
  data() {
    return {
      openSubmenu: '',
      regularRhraseTaxIdPF:
        '^(([A-ZÑ&]{4})([0-9]{2})([0][13578]|[1][02])(([0][1-9]|[12][\\d])|[3][01])([A-Z0-9]{3}))|' +
        '(([A-ZÑ&]{4})([0-9]{2})([0][13456789]|[1][012])(([0][1-9]|[12][\\d])|[3][0])([A-Z0-9]{3}))|' +
        '(([A-ZÑ&]{4})([02468][048]|[13579][26])[0][2]([0][1-9]|[12][\\d])([A-Z0-9]{3}))|' +
        '(([A-ZÑ&]{4})([0-9]{2})[0][2]([0][1-9]|[1][0-9]|[2][0-8])([A-Z0-9]{3}))$',
      regularRhraseTaxIdPM:
        '^(([A-ZÑ&]{4})([0-9]{2})([0][13578]|[1][02])(([0][1-9]|[12][\\d])|[3][01])([A-Z0-9]{3}))|' +
        '(([A-ZÑ&]{4})([0-9]{2})([0][13456789]|[1][012])(([0][1-9]|[12][\\d])|[3][0])([A-Z0-9]{3}))|' +
        '(([A-ZÑ&]{4})([02468][048]|[13579][26])[0][2]([0][1-9]|[12][\\d])([A-Z0-9]{3}))|' +
        '(([A-ZÑ&]{4})([0-9]{2})[0][2]([0][1-9]|[1][0-9]|[2][0-8])([A-Z0-9]{3}))$',
      activeSettingsGear: false,
    }
  },
  props: {
    client: {
      type: Object,
      default: null,
    },
  },
  computed: {
    menu() {
      return {
        dashboard: {
          title: 'Inicio',
          to: { name: 'homeAccountingView' },
          icon: 'stars',
          iconSVG: '',
          typeArrow: 'keyboard_arrow_right',
          isActive:
            this.openSubmenu === 'Mi Contabilidad' ||
            this.$route.name === 'homeAccountingView',
          containerName: 'home',
        },
        journal: {
          title: 'Pólizas',
          to: { name: 'journalEntriesView' },
          icon: 'playlist_add_check',
          iconSVG: '',
          typeArrow: 'keyboard_arrow_down',
          isActive:
            this.openSubmenu === 'polizas' ||
            this.$route.name === 'journalEntriesView',
          containerName: 'myjournalEntries',
        },
        trialBalance: {
          title: 'Balanza de Comprobación',
          to: { name: 'trialBalanceView' },
          icon: '',
          iconSVG: require('@assets/icons/balanza1.svg'),
          typeArrow: 'keyboard_arrow_right',
          isActive:
            this.openSubmenu === 'Balanza de Comprobación' ||
            this.$route.name === 'myTeam',
          containerName: 'myBalance',
        },
        balanceSheet: {
          title: 'Balance General',
          to: { name: 'balanceSheetView' },
          icon: '',
          iconSVG: require('@assets/icons/balance.svg'),
          typeArrow: 'keyboard_arrow_right',
          isActive:
            this.openSubmenu === 'Balance General' ||
            this.$route.name === 'myTeam',
          containerName: 'myBalance',
        },
        incomeStatement: {
          title: 'Estado de Resultados',
          to: { name: 'IncomeStatementView' },
          icon: '',
          iconSVG: require('@assets/icons/resultados.svg'),
          typeArrow: 'keyboard_arrow_right',
          isActive:
            this.openSubmenu === 'Estado de Resultados' ||
            this.$route.name === 'myTeam',
          containerName: 'myBalance',
        },
      }
    },
    legalName() {
      if (this.client.legalName) return this.client.legalName
      return this.client.taxId.match(this.regularRhraseTaxIdPF) !== null
        ? 'Nombre sin configurar'
        : 'Razón social sin configurar'
    },
    activeSettings() {
      return this.activeSettingsGear
    },
  },
  methods: {
    onClickMenuItem(menuItem) {
      if (menuItem.to && this.$route.name != menuItem.to.name) {
        this.$router.push(menuItem.to)
      }
      this.openSubmenu =
        this.openSubmenu === menuItem.title ? '' : menuItem.title
      this.activeSettingsGear = false
    },
    goToLedgerAccounts() {
      this.activeSettingsGear = true
      this.$router.push({ name: 'ledgerAccountsView' })
    },
  },
}
</script>

<template>
  <div class="w-80 bg-asideAccounting pt-8 text-black">
    <div class="h-19 bg-asideAccountingHeader pt-4 pl-4 text-base">
      <div class="flex justify-between">
        <div class="">
          <span class="font-bold">{{ client.taxId }}</span>
        </div>
        <div class="">
          <GxIcon
            :icon="'settings'"
            class="cursor-pointer mr-2"
            :class="{
              'text-gearConfiguration': !activeSettings,
              'text-gearConfiguration-click': activeSettings,
            }"
            :title="'Settings'"
            v-bind:style="{ fontSize: '18px' }"
            @click="goToLedgerAccounts"
          />
        </div>
      </div>
      <div
        ><span
          :class="{
            'text-sm': this.client.legalName ? false : true,
          }"
        >
          {{ legalName }}</span
        >
      </div>
    </div>

    <div>
      <ul class="py-2">
        <template v-for="menuItem in menu">
          <li :key="menuItem.name" @click="onClickMenuItem(menuItem)">
            <div
              class="flex justify-start w-full py-3 pl-4 cursor-pointer gx-menu-item group hover:bg-asideAccountingHeader h-10"
              :class="{
                'group-hover:text-black': !menuItem.isActive, //&& !someChildrenIsActive(menuItem),
                'gx-menu-item--active': menuItem.isActive,
              }"
            >
              <!-- @click="onClickMenuItem(menuItem)" -->
              <!-- // -->
              <GxIcon
                v-if="menuItem.icon"
                class="z-1 -mt-px"
                :icon="menuItem.icon"
                v-bind:style="{ fontSize: '18px' }"
                :class="{
                  'group-hover:text-primary-600': !menuItem.isActive,
                  'text-black': menuItem.isActive,
                  'text-gray-700': !menuItem.isActive,
                }"
              />
              <div
                v-else
                class="z-1 -mt-px ml-px mr-2px"
                :class="{
                  'group-hover:text-primary-600': !menuItem.isActive,
                  'text-primary': menuItem.isActive,
                  'text-black': !menuItem.isActive,
                  'text-gray-700': !menuItem.isActive,
                }"
              >
                <img :src="menuItem.iconSVG" />
              </div>
              <!-- v-if="!isCollapsed" -->
              <span
                class="mx-2 -mt-1 text-base w-200"
                :class="{
                  'hover:bg-asideAccountingHeader group-hover:text-primary-600': !menuItem.isActive, //&& !someChildrenIsActive(menuItem),
                  'gx-menu-item--active font-bold': menuItem.isActive,
                }"
              >
                {{ menuItem.title }}
              </span>

              <div
                class="flex justify-start w-50 py-1 pl-4 cursor-pointer gx-menu-item group hover:bg-asideAccountingHeader"
                :class="{
                  'group-hover:text-primary-600': !menuItem.isActive, //&& !someChildrenIsActive(menuItem),
                  'gx-menu-item--active': menuItem.isActive,
                }"
              >
                <!-- @click="onClickMenuItem(menuItem)" -->

                <GxIcon
                  v-if="menuItem.isActive"
                  class="flex justify-end flex-grow pr-3 -mt-1"
                  :class="{
                    'group-hover:text-primary-600': !menuItem.isActive,
                  }"
                  v-bind:style="{ fontSize: '18px' }"
                  :icon="menuItem.typeArrow"
                />
                <!-- false //menuItem.isActive || someChildrenIsActive(menuItem)
                      ? 'keyboard_arrow_up'
                      : 'keyboard_arrow_right' -->
                <!-- v-if="menuItem.children" -->
              </div>
            </div>
            <div
              v-if="menuItem.isActive"
              class="flex flex-col items-center content-center"
              :name="menuItem.containerName"
            >
              <slot :name="menuItem.containerName"> </slot>
            </div>
          </li>
        </template>
      </ul>
    </div>
    <div class="flex fixed flex-col items-center justify-center bottom-0">
      <img
        class="w-21 mb-5 ml-28"
        src="@src/assets/images/Logos/black.svg"
        alt="logo"
      />
    </div>
  </div>
</template>
